// canvas.js

import React, { Component } from "react"
import { v4 } from "uuid"
import styles from "./canvas.module.css"

class Touch {
  constructor(touch, identifier = null) {
    this.start = { ...touch }
    this.points = []
    this.identifier = identifier
  }

  push(point) {
    this.points.push({
      start: { ...this.start },
      stop: { ...point },
    })
    this.start = { ...point }
  }
}

class Canvas extends Component {
  constructor(props) {
    super(props)
    this.onMouseDown = this.onMouseDown.bind(this)
    this.onMouseMove = this.onMouseMove.bind(this)
    this.endPaintEvent = this.endPaintEvent.bind(this)

    this.onMouseOut = this.onMouseOut.bind(this)
    this.onMouseEnter = this.onMouseEnter.bind(this)

    this.resizeCanvas = this.resizeCanvas.bind(this)
    this.onTouchStart = this.onTouchStart.bind(this)
    this.onTouchMove = this.onTouchMove.bind(this)
    this.onTouchEnd = this.onTouchEnd.bind(this)
  }

  resizeCanvas() {
    // Here we set up the properties of the canvas element.
    this.canvas.width = document.body.clientWidth
    this.canvas.height = document.body.clientHeight
    this.ctx = this.canvas.getContext("2d")
    this.ctx.lineJoin = "round"
    this.ctx.lineCap = "round"
    this.ctx.lineWidth = 1

    /**
     * Your drawings need to be inside this function otherwise they will be reset when
     * you resize the browser window and the canvas goes will be cleared.
     */
    this.full_paint()
  }

  flatten_to_x_y(start_stop_array) {
    let line = start_stop_array
    let points = line.map(({ start, stop }) => ({
      x: start.offsetX,
      y: start.offsetY,
    }))
    points = points.concat({
      x: line[line.length - 1].stop.offsetX,
      y: line[line.length - 1].stop.offsetY,
    })
    return points
  }

  /*
  Generates the sticky bits from a line
  */
  generateStrokes(line) {
    if (line.length === 0) {
      return
    }

    let points = this.flatten_to_x_y(line)
    let connectors = []

    // the last segment
    connectors.push({
      start: {
        offsetX: points[points.length - 2].x,
        offsetY: points[points.length - 2].y,
      },
      stop: {
        offsetX: points[points.length - 1].x,
        offsetY: points[points.length - 1].y,
      },
    })

    for (var i = 0, len = points.length; i < len; i++) {
      let dx = points[i].x - points[points.length - 1].x
      let dy = points[i].y - points[points.length - 1].y
      let d = dx * dx + dy * dy

      if (d < 1000) {
        connectors.push({
          start: {
            offsetX: points[points.length - 1].x + dx * 0.2,
            offsetY: points[points.length - 1].y + dy * 0.2,
          },
          stop: {
            offsetX: points[i].x - dx * 0.2,
            offsetY: points[i].y - dy * 0.2,
          },
        })
      }
    }

    return connectors
  }

  allConnectors = []

  full_paint() {
    this.allConnectors.forEach(({ connectors, styles }) => {
      styles.forEach(style => {
        this.paintConnectors(connectors, style)
      })
    })
  }

  /*
  Paints the last segment

  And any connectors from that last point
  */
  incremental_paint() {
    if (this.line.length === 0) {
      return
    }

    let connectors = this.generateStrokes(this.line)
    this.paintConnectors(connectors, {
      strokeStyle: "rgba(0,30,240,0.3",
    })
    this.paintConnectors(connectors, {
      strokeStyle: "rgba(0,130,200,0.3",
      offset: 5,
    })

    this.allConnectors = this.allConnectors.concat({
      connectors,
      styles: [
        {
          strokeStyle: "rgba(0,30,240,0.3",
        },
        {
          strokeStyle: "rgba(0,130,200,0.3",
          offset: 5,
        },
      ],
    })
  }

  paintConnectors(connectors, style) {
    let { strokeStyle, offset } = style

    // Default values
    strokeStyle = strokeStyle || "rgba(0,0,0,0.3)"
    offset = offset || 0

    // Draw!@
    let ctx = this.ctx
    connectors.forEach(({ start, stop }) => {
      ctx.beginPath()
      ctx.strokeStyle = strokeStyle
      ctx.moveTo(start.offsetX + offset, start.offsetY + offset)
      ctx.lineTo(stop.offsetX + offset, stop.offsetY + offset)
      ctx.stroke()
    })
  }

  isPainting = false
  // Different stroke styles to be used for user and guest
  userStrokeStyle = "#EE92C205"
  guestStrokeStyle = "#F0C987"
  line = []
  strokes = []

  // v4 creates a unique id for each user. We used this since there's no auth to tell users apart
  userId = v4()

  ongoingTouches = []
  onTouchStart(nativeEvent) {
    console.log("touchstart.")
    let touches = nativeEvent.changedTouches
    for (let i = 0; i < touches.length; i++) {
      let { clientX, clientY, identifier, target } = touches[i]
      this.isPainting = true
      this.ongoingDrags.push(
        // clientX/Y gives X/Y relative to the viewport, in pixels
        // pageX/Y gives X/Y relative to the HTML element, in pixels
        // Difference - when you're scrolled down, for example!

        new Touch(
          {
            offsetX: clientX - target.offsetLeft,
            offsetY: clientY - target.offsetTop,
          },
          identifier
        )
      )
    }
  }

  ongoingTouchIndexById(ongoingTouches, idToFind) {
    for (var i = 0; i < ongoingTouches.length; i++) {
      var id = ongoingTouches[i].identifier

      if (id === idToFind) {
        return i
      }
    }
    return -1 // not found
  }

  onTouchMove(nativeEvent) {
    let touches = nativeEvent.changedTouches

    for (let i = 0; i < touches.length; i++) {
      let { clientX, clientY, identifier, target } = touches[i]

      // Find the touch
      let idx = this.ongoingTouchIndexById(this.ongoingDrags, identifier)
      if (idx < 0) {
        console.log("No touch!", identifier)
        return
      }

      // Modify it
      this.ongoingDrags[idx].push({
        offsetX: clientX - target.offsetLeft,
        offsetY: clientY - target.offsetTop,
      })

      // Repaint!
      this.line = this.ongoingDrags[idx].points
      this.incremental_paint()
    }
  }

  // same as onMouseOut
  onTouchEnd() {
    if (this.isPainting) {
      this.sendPaintData()
    }
  }

  ongoingDrags = []
  onMouseEnter({ nativeEvent }) {
    // offsetX/Y gives X/Y relative to the Target element!
    // so if the event was bubbled from something else, we have to compensate!
    const { offsetX, offsetY } = nativeEvent
    if (this.isPainting) {
      this.ongoingDrags.push(
        new Touch({
          offsetX: offsetX - this.offsetLeft,
          offsetY: offsetY - this.offsetTop,
        })
      )
    }
  }

  onMouseOut() {
    if (this.isPainting) {
      this.sendPaintData()
    }
  }

  startPainting({ offsetX, offsetY }) {
    this.isPainting = true
    this.ongoingDrags.push(new Touch({ offsetX, offsetY }))
  }

  onMouseDown({ nativeEvent }) {
    const { offsetX, offsetY } = nativeEvent
    this.endPaintEvent()
    this.startPainting({ offsetX, offsetY })
  }

  onMouseMove({ nativeEvent }) {
    console.log(this.ongoingDrags)
    if (this.isPainting) {
      const { offsetX, offsetY } = nativeEvent
      // Update the current drag
      this.ongoingDrags[0].push({ offsetX, offsetY })

      this.line = this.ongoingDrags[0].points
      this.incremental_paint()
    }
  }
  endPaintEvent() {
    if (this.isPainting) {
      this.isPainting = false
      this.sendPaintData()
    }
  }

  async sendPaintData() {
    // const body = {
    //   line: this.line,
    //   userId: this.userId,
    // }
    // // We use the native fetch API to make requests to the server
    // const req = await fetch("http://localhost:4000/paint", {
    //   method: "post",
    //   body: JSON.stringify(body),
    //   headers: {
    //     "content-type": "application/json",
    //   },
    // })
    // const res = await req.json()
    // this.line = []

    this.strokes = this.strokes.concat({
      line: this.ongoingDrags.pop().points,
    })

    console.log("sending...")
  }

  componentDidMount() {
    window.addEventListener("resize", this.resizeCanvas, false)
    let canvas = this.canvas

    // Prevent scrolling when touching the canvas
    document.body.addEventListener(
      "touchstart",
      function(e) {
        if (e.target === canvas) {
          e.preventDefault()
        }
      },
      { passive: false }
    )
    document.body.addEventListener(
      "touchend",
      function(e) {
        if (e.target === canvas) {
          e.preventDefault()
        }
      },
      { passive: false }
    )
    document.body.addEventListener(
      "touchmove",
      function(e) {
        if (e.target === canvas) {
          e.preventDefault()
        }
      },
      { passive: false }
    )

    this.resizeCanvas()
  }

  render() {
    return (
      <canvas
        className={styles.canvas}
        // We use the ref attribute to get direct access to the canvas element.
        ref={ref => (this.canvas = ref)}
        onMouseDown={this.onMouseDown}
        onMouseUp={this.endPaintEvent}
        onMouseMove={this.onMouseMove}
        onMouseEnter={this.onMouseEnter}
        onMouseOut={this.onMouseOut}
        // for accessibility
        onBlur={this.onMouseOut}
        // We also need touch events!
        onTouchStart={this.onTouchStart}
        onTouchMove={this.onTouchMove}
        onTouchEnd={this.onTouchEnd}
      />
    )
  }
}
export default Canvas
